import { mapGetters, mapActions, mapMutations } from 'vuex'

export default {
    name: 'slug',
    data() {
        return {
            urlApi: 'http://194.87.111.244/api/',
            count: 1,
        }
    },

    methods: {
        ...mapActions('product', ['fetchProductItem']),
        ...mapMutations('card', ['addProductInCart']),
    },

    computed: {
        ...mapGetters('product', [
            'productItem',
            'productSEOTitle',
            'productSEODescription',
        ]),
    },

    async fetch() {
        if (process.client) {
            document.body.scrollTop = document.documentElement.scrollTop = 0
        }

        try {
            await this.fetchProductItem(this.$route.params.slug)
        } catch {
            this.$nuxt.error({ statusCode: 404 })
        }
    },

    head() {
        return {
            title: this.productSEOTitle + ' - hearth.ceramics',
            meta: [
                {
                    hid: 'description',
                    name: 'description',
                    content: this.productSEODescription,
                },
            ],
        }
    },
}
